import { kv } from '@vercel/kv';

export interface UserSubmission {
  id: string;
  name: string;
  email: string;
  ipAddress: string;
  createdAt: string;
  modelId?: string;
}

const SUBMISSIONS_KEY = 'submissions';

export async function createSubmission(data: Omit<UserSubmission, 'id' | 'createdAt'>): Promise<UserSubmission> {
  const id = `sub_${Date.now()}_${Math.random().toString(36).substring(7)}`;
  const submission: UserSubmission = {
    id,
    ...data,
    createdAt: new Date().toISOString(),
  };

  // Store the submission with its ID as the key
  await kv.hset(SUBMISSIONS_KEY, { [id]: JSON.stringify(submission) });

  return submission;
}

export async function getAllSubmissions(): Promise<UserSubmission[]> {
  const submissions = await kv.hgetall(SUBMISSIONS_KEY);

  if (!submissions) {
    return [];
  }

  return Object.values(submissions)
    .map((str) => JSON.parse(str as string) as UserSubmission)
    .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
}

export async function deleteSubmission(id: string): Promise<boolean> {
  const result = await kv.hdel(SUBMISSIONS_KEY, id);
  return result > 0;
}

export async function getSubmissionById(id: string): Promise<UserSubmission | null> {
  const submission = await kv.hget(SUBMISSIONS_KEY, id);

  if (!submission) {
    return null;
  }

  return JSON.parse(submission as string) as UserSubmission;
}
